import React,{useContext, useState, useEffect} from 'react'
import { GoogleMap, useJsApiLoader } from '@react-google-maps/api';
import {keyGoogle} from '../../keyAPI'
import {mobileContext} from "../../../App";


function Map(props) {

    const [coords, setCoords] = useState({lon:0, lat:0});

    const { isLoaded } = useJsApiLoader({
        id: 'google-map-script',
        googleMapsApiKey: `${keyGoogle}`
    })

    let isMobile = useContext(mobileContext)
    const containerStyle = {
        width: '100%',
        height:  isMobile? '300px' : '590px'
    };

    const [map, setMap] = React.useState(null)

    const onLoad = React.useCallback(function callback(map) {
        setCoords({lon:props.lon, lat:props.lat});
        const bounds = new window.google.maps.LatLngBounds();
        map.fitBounds(bounds);
        setMap(map)
        console.log(coords, "onLoad6 timeout")
    }, [coords.lat, coords.lng])

    useEffect(() => {
        setCoords({lon:props.lon, lat:props.lat});
    }, [props.lon, props.lat]);

    const onUnmount = React.useCallback(function callback(map) {
        setMap(null)
    }, [])

    return isLoaded ? (
        <div id="mapWrapper">
            <GoogleMap
                className="map"
                mapContainerStyle={containerStyle}
                center={{
                    lat: coords.lat,
                    lng: coords.lon
                }}
                // defaultCenter={{ lat: coords.lat, lng: coords.lon }}
                zoom={11}
                onLoad={onLoad}
                onUnmount={onUnmount}
                style={{borderRadius: '100px'}}
            >
                { /* Child components, such as markers, info windows, etc. */ }
                <></>
            </GoogleMap>
        </div>
    ) : <></>
}

export default Map