import './App.css';
import CurrentWeatherForOneLocationMain
    from "./Components/CurrentWeatherForOneLocation/CurrentWeatherForOneLocationMain";
import React, {useEffect, useState} from 'react';
import './Styles/Style.sass'

import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";
import MinuteWeatherForOneLocation from "./Components/MinuteWeatherForOneLocation/MinuteWeatherForOneLocation";
import Footer from "./Components/GlobalComponents/Footer";
import Navigation from "./Components/GlobalComponents/Navigation";

export const mobileContext = React.createContext(false);
export const currentCityContext = React.createContext(6695624);

//API KEY========================================
// export const key = 'c1e7782de6f05ad9962f8946d6f491f3';
// export const keyAPI = React.createContext(key);
//===============================================

function App() {
    const [currentSelectedId, setCurrentSelectedId] = useState(6695624); //WWA
    const changeHandler = (e) =>{
        setCurrentSelectedId(e.target.value);
    }


    //Resize okna, aby dynamicznie zmieniać stylowanie na mobilne
    //UseEffect na każdy resize zmienia informacje o tym czy okno jest mniejsze/ większe niż 1024px

    const mobileString = "(max-width: 1024px)";
    const [isMobile, setIsMobile] = useState({ mobile: window.matchMedia(mobileString).matches });

    useEffect(() => {
        const handler = () => {
            setIsMobile({mobile: window.matchMedia(mobileString).matches});
            console.log(isMobile);
        };

        const mouseHandler= () =>{
            setIsMobile({mobile: window.matchMedia(mobileString).matches});
            console.log(isMobile);
            window.removeEventListener('mousemove', mouseHandler);
        }

        window.addEventListener('resize', handler);
        window.addEventListener('mousemove', mouseHandler);

        return () => {
            window.removeEventListener('resize', handler);
        }
    })

    //Koniec sekcji odpowiedzialnej za resize i mobilke

    return (
    <Router className="App">
        <mobileContext.Provider value={isMobile.mobile}>
            <currentCityContext.Provider value={currentSelectedId}>
                <Navigation currentSelectedId={currentSelectedId} changeHandler={changeHandler}/>
                <Switch>
                    <Route path={process.env.PUBLIC_URL + '/'}>
                        <CurrentWeatherForOneLocationMain currentSelectedId={currentSelectedId}/>
                    </Route>
                    <Route path={process.env.PUBLIC_URL + './currentWeather'}>
                        <CurrentWeatherForOneLocationMain currentSelectedId={currentSelectedId}/>
                    </Route>
                    <Route path="./minuteWeather" disabled={true}>
                        <MinuteWeatherForOneLocation/>
                    </Route>

                    {/*<Route path="/">*/}
                    {/*    <>*/}
                    {/*</Route>*/}
                </Switch>
                <Footer/>
            </currentCityContext.Provider>
        </mobileContext.Provider>

    </Router>

  );
}

export default App;
