import React from 'react';
import {Link} from "react-router-dom";

function MenuLinks() {
    return(
        <div className="menu">
            <Link to="/currentWeather">
                <div className="menu-item">Aktualna pogoda</div>
            </Link>

            <Link to="/minuteWeather">
                <div className="menu-item">Prognoza minutowa</div>
            </Link>

            <Link to="/twoDaysWeather">
                <div className="menu-item">Prognoza dwudniowa</div>
            </Link>

            <Link to="/weekWeather">
                <div className="menu-item">Prognoza tygodniowa</div>
            </Link>
        </div>
    )
}

export default MenuLinks;