import React,{useEffect} from 'react';
import $ from "jquery";

function MobileCountryForm(props) {

    const toggleHandler = () => {
        $(".menu").slideToggle(300);
    }
    useEffect(() => {

        const scrollHideHandler = () => {
            console.log("scrollHideHandler")
            $(".menu").slideUp(300);
        }

        window.addEventListener('scroll', scrollHideHandler);
    },[])


    return(
        <form>
            <select value={props.currentSelectedId} onChange={props.changeHandler}>
                {
                    props.sortedCities.map(city =>(
                        <option key={city.id} value={city.id}>{city.city}</option>
                    ))
                }
            </select>
            <div className="toggle" onClick={toggleHandler}> MENU </div>
        </form>
    )
}

export default React.memo(MobileCountryForm);