import React,{useState, useEffect, useContext} from 'react';
import {key} from "../keyAPI";
import axios from "axios";
import {TimestampToDate} from "../../Functions/TimestampToDate";
import {CoordinatesFormat} from '../../Functions/CoordinatesFormat';
import ReactLoading from 'react-loading';
import "../../Styles/CurrentWeatherForOneLocation/Current.sass"
import arrow from "../../Images/arrow.png"
import sunrise from "../../Images/sunrise.png"
import sunset from "../../Images/sunset.png"
import Map from "./CurrentWeatherForOneLocationComponents/Map";
import {currentCityContext} from "../../App";

function CurrentWeatherForOneLocationMain(){

    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(false);

    const currentCityId = useContext(currentCityContext)

    const [data, setData] = useState({
        name: 'City',
        dt : 0,
        coord :{
            lon: 0,
            lat: 0
        },
        sys: {
            country: 'World',
            sunrise:0,
            sunset:0
        },
        weather: [{
            main : "Clear",
            description: "clear sky",
            icon: "01d"
        }],
        wind : {
            speed: 1,
            deg: 0
        },
        clouds:{
            all:1
        },
        main : {
            temp : 0,
            feels_like : 0,
            temp_min : 0,
            temp_max : 0,
            pressure : 0,
        },
    });

    const timer = () => {setLoading(false);setError(false);}

    const lang = 'pl';
    //4839416
    useEffect(() => {
        axios.get(`https://api.openweathermap.org/data/2.5/weather?id=${currentCityId}&lang=${lang}&appid=${key}`)
            .then(response => {
                setLoading(true);
                setData(response.data)
                // console.log(response.data)
                //convert Kelvins to Celcius
                setData(prevState=>({
                    ...prevState,
                    main:{
                        temp : Math.floor(prevState.main.temp-273),
                        feels_like : Math.floor(prevState.main.feels_like-273),
                        temp_min : Math.floor(prevState.main.temp_min-273),
                        temp_max : Math.floor(prevState.main.temp_max-273),
                    }
                }))
                setTimeout(timer,1000);

                // let root = document.documentElement;
                // root.style.setProperty('--deg-to-rotate', data.wind.deg + "deg");
                // root.style.setProperty('--deg-to-start', (0-data.wind.deg) + "deg");

            })
            .catch(err =>{
                console.log(err)
                setError(true);
                setLoading(false);
            })

            return () => {
                clearTimeout(timer)
            }

    },[currentCityId])

    return(
        <React.Fragment>
            {loading && <div className="boxLoader"><ReactLoading className="pageLoader" type="spinningBubbles" color="#4269f5" height={667} width={375} /></div>}
            {!loading && <div className="current">

                <div className="left">
                    <Map lat={data.coord.lat} lon={data.coord.lon}/>
                    <div className="place">
                        <h1>{data.sys.country}</h1>
                        <div>{data.name}</div>
                    </div>
                    <div className="coord">
                        <div>{CoordinatesFormat(data.coord.lat, "N")} {CoordinatesFormat(data.coord.lon, "E")}</div>
                    </div>
                </div>

                <div className="right">
                    <div className="sys">
                        <div>
                            <img src={sunrise} alt="sunrise-icon"/>
                            {TimestampToDate(data.sys.sunrise)}
                        </div>
                        <div>
                            <img src={sunset} alt="sunset-icon"/>
                            {TimestampToDate(data.sys.sunset)}
                        </div>
                    </div>

                    <div className="weather">
                        <img src={`http://openweathermap.org/img/wn/${data.weather[0].icon}@2x.png`}/>
                        <div key={data.weather[0].description.id}>{data.weather[0].description}</div>
                    </div>

                    {/*{*/}
                    {/*    data.weather.map(place =>*/}
                    {/*        <div className="weather">*/}
                    {/*            <div key={place.description.id}>{place.description}</div>*/}
                    {/*            <img src={`http://openweathermap.org/img/wn/${place.icon}@2x.png`}/>*/}
                    {/*        </div>*/}
                    {/*    )*/}
                    {/*}*/}

                    <div className="windClouds">
                        <div>
                            <p>Wiatr:</p>
                            <p>{data.wind.speed} m/s <img id="windArrow" src={arrow} alt="arrow-wind" style={{transform: `rotate(${data.wind.deg}deg)`}}/></p>
                        </div>

                        <div>
                            <p>Zachmurzenie: </p>
                            <p>{data.clouds.all} %</p>
                        </div>
                    </div>

                    <div className="main">
                        <div>
                            <p>Temperatura:</p>
                            <p>{data.main.temp}°C</p>
                        </div>
                        <div>
                            <p>Temperatura odczuwalna:</p>
                            <p>{data.main.feels_like}°C</p>
                        </div>
                        <div>
                            <p>Temperatura maksymalna:</p>
                            <p>{data.main.temp_max}°C</p>
                        </div>
                        <div>
                            <p>Temperatura minimalna:</p>
                            <p>{data.main.temp_min}°C</p>
                        </div>
                    </div>

                    <div className="dt">Czas pomiaru {TimestampToDate(data.dt)}</div>
                </div>















            </div>}
        </React.Fragment>

    )
}

export default CurrentWeatherForOneLocationMain;


