import React from 'react';
import {Link} from "react-router-dom";

function Links() {
    return(
        <React.Fragment>
            <Link to="/currentWeather">
                <button>Aktualna pogoda</button>
            </Link>

            <Link to="/minuteWeather">
                <button>Prognoza minutowa</button>
            </Link>

            <Link to="/twoDaysWeather">
                <button>Prognoza dwudniowa</button>
            </Link>

            <Link to="/weekWeather">
                <button>Prognoza tygodniowa</button>
            </Link>
        </React.Fragment>
    )
}

export default Links;