import React,{useState, useEffect, useContext} from 'react';
import Logo from "./Logo";
import CountryForm from "./Form/CountryForm";
import cityListJSON from "../../DataJSON/myCities.json";
import MobileCountryForm from "./Form/MobileCountryForm";
import MenuLinks from "./Form/MenuLinks";
import {mobileContext} from "../../App";

function Navigation(props){

    //state formularza:
    const [cities, setCities] = useState([]);
    const [sortedCities, setSortedCities] = useState([]);

    useEffect(() => {
        cityListJSON.map(elem => {
            setCities(prevState => ([
                ...prevState,
                {
                    id : elem.id,
                    city: elem.city
                }
            ]));
        })

    },[])

    useEffect(()=>{
        cities.sort(function(a, b){
            if(a.city < b.city) { return -1; }
            if(a.city > b.city) { return 1; }
            return 0;
        });
    },[cities])

    useEffect(() => {
        setSortedCities(cities);
    },[cities])

    //koniec sekcji state form

    const isMobile = useContext(mobileContext);

    return (
        <React.Fragment>
            {!isMobile && <nav className="desktop">
                <div className="responsive">
                    <Logo/>
                    <CountryForm currentSelectedId={props.currentSelectedId} changeHandler={props.changeHandler} sortedCities={sortedCities}/>
                </div>
            </nav>}

            {isMobile && <React.Fragment>
                <nav className="mobile">
                    <Logo/>
                    <MobileCountryForm currentSelectedId={props.currentSelectedId} changeHandler={props.changeHandler} sortedCities={sortedCities}/>
                </nav>
                <MenuLinks/>
            </React.Fragment>
            }
        </React.Fragment>
    )

}

export default Navigation;